.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1 {
  font-size: 2rem;
  margin-bottom: 20px;
  text-align: center;
  color: #0d5e87;
}

/* Estilos de la lista de publicaciones */
ul {
  list-style-type: none;
  padding: 0;
}
/* Estilos para cada post */
li {
  background-color: #fff; /* Fondo blanco para cada post */
  border: 1px solid #ddd; /* Borde gris claro */
  border-radius: 8px; /* Bordes redondeados */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Sombra suave */
  margin-bottom: 15px; /* Espacio entre posts */
  padding: 15px;
  transition: transform 0.2s, box-shadow 0.2s;
}
/* Efecto “hover” para cada post */
li:hover {
  /* Eleva un poco el post al pasar el cursor*/
  transform: translateY(-5px);
}
/* Título de cada publicación (selector padre/hijo) */
li h2 {
  font-size: 14px;
  color: #333;
  margin-bottom: 10px;
}
/* Contenido de la publicación (selector padre/hijo) */
li p {
font-size: 1rem;
color: #555;
margin-bottom: 15px;
line-height: 1.5; /* Mejora la legibilidad */
}
